@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@600&display=swap);
body {
  margin: 0;
  font-family: 'Montserrat', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.HeaderContainer {
    padding: 10px;
    text-align: center;
    background: rgb(247, 57, 57);
    color: #FFF !important;
    font-size: 17px;
    width: 100%;
    position: fixed;
    z-index: 1000;
  }
  
  .nav-nome {
    font-family: 'Montserrat', Arial, Helvetica, sans-serif;
    font-size: 20px;
    text-decoration: none !important;
    color: #FFF;
    cursor: default;
  }
  
  /*Ver se fica mais bonito com ou sem*/
  
  .nav-nome:hover {
    color: rgba(226, 228, 228, 0.9);
  }
  
  .margem-header {
    margin-bottom: 30px;
    z-index: 1000;
  }

  /*Lembrar de inserir media query para margem-header*/

.MatchContainer{
    font-family: 'Montserrat', Arial, Helvetica, sans-serif;
}

.margem-header{
    padding-top: 5%;
}

@media only screen and (max-width: 768px) {
    .margem-header{
        padding-top: 10%;
    }
  }


.logo{
    width: 80%;
    border-radius: 10%;
}

p{
    margin: 0,5 !important;
}

.lineup{
    padding: 0px !important;
}

.label:after {
    content: '\A';
    white-space: pre;
}

.logos-e-lineups{
    display: flow-root !important;
}

.logo-devils{
    float: left;
}

.logo-adversario{
    float: right;
    z-index: 1001;
}

.dificuldade-cor{
    color: rgb(240, 110, 24);
    font-weight: bolder;
}

